.status {
    padding: 16px 8px;
    background-color: white;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    margin: 0 0 10px 0;
}

.edit {
    display: block;
    width: 100%;
    box-sizing: border-box;
    background-color: white;
    box-sizing: border-box;
    color: black;
    border: none;
    padding: 8px 16px;
    resize: vertical;
    margin: 0 0 8px 0;
    border-bottom: 1px solid black;
    outline: none;
    cursor: text;
}

.text {
    cursor: pointer;
}